import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'
import Cart from "../components/Checkoutshopcart/Checkoutshopcart.vue";
import Main from "../components/main/main.vue";
import Filtershopcart from "../components/filtershopcart/filtershopcart.vue";
Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'main',
    component: Main,

  },
  {
    path: '/cart',
    name: 'cart',
    component: Cart,
  },
  {
    path: '/filtershopcart',
    name: 'filtershopcart',
    component: Filtershopcart,

  },

]

const router = new VueRouter({
  routes
})

export default router
