import Slideshow from "../Slideshow/Slideshow.vue"
import Shopitem from "../shopitem/shopitem.vue"
export default {

    data() {
        return {

        }
    }, mounted() {

    },
    components: {
        Slideshow,
        Shopitem
    },
    methods: {
        enter(e) {
            e.enter = false;
        },
        exit(e) {
            e.enter = true;
        },
        addshopcartitem(item) {
            this.$store.commit('additem', item)
        },
    },
    computed: {
        shopdata() {

            return this.$store.state.shopitem;
        }

    }
}

