export default {
    data() {
        return {
            arr: null,

        }
    },
    mounted() {
        this.arr = JSON.parse(JSON.stringify(this.$store.state.shopcart));
    },
    methods: {
        additemnum(id) {
            this.shopcartitem.forEach(e => {
                if (e.id == id) {
                    e.count++;
                }
            })
        },
        subitemnum(id) {
            this.shopcartitem.forEach(e => {
                if (e.id == id) {
                    e.count = e.count > 0 ? --e.count : 0;
                }
            })
        },
        changeitem(item) {
            this.$store.commit("changeitemnum", this.shopcartitem);
        },
        removeitem(id) {

            this.$store.commit("removeitem", id);

        },
        additem(id) {
            this.$store.commit("additem", id);

        },
        updateitem() {
            this.arr = JSON.parse(JSON.stringify(this.$store.state.shopcart));
        }
    },
    computed: {
        shopcartitem() {


            return this.arr;
        },
        shopcartdata() {
            return this.$store.state.shopcart;
        },
        hotshopitem() {
            return this.$store.state.hotshopitem;
        },
        sumcart() {
            let arr = this.$store.state.shopcart;
            let sum = 0;
            arr.forEach(e => {
                sum += e.count * parseInt(e.contenttext.price.replaceAll(",", ""));
            });
            return sum;
        }
    },
    watch: {
        shopcartdata: function (cur, pre) {
            this.arr = JSON.parse(JSON.stringify(this.shopcartdata));
        },
    }


}