export default {
    props: ["filter"],
    data() {
        return {

        }
    }, mounted() {
        console.log();
    },
    methods: {
        enter(e) {
            e.enter = false;
        },
        exit(e) {
            e.enter = true;
        },
        addshopcartitem(item) {
            this.$store.commit('additem', item)
        },
    },
    computed: {
        shopdata() {
            let filter = this.filter;
            if (filter != undefined) {
                return this.$store.state.shopitem.filter(

                    e => {
                        if (e.contenttext.text.indexOf(filter) != -1) {
                            return true;
                        }
                        return false;
                    });
            }
            else
                return this.$store.state.shopitem;
        }

    }
}

