export default {
    data() {
        return {
            openactive: false,
        }
    }, methods: {
        openmenu() {
            this.openactive = !this.openactive;
        },
        removeitem(item) {
            this.$store.commit("removeitem", item);
        }
    }, computed: {
        shopcartcount() {
            return this.$store.state.shopcart.length;
        },
        shopcartitem() {
            return this.$store.state.shopcart;
        }, sumtotal() {
            let sum = 0;
            this.shopcartitem.forEach(e => {
                let count = e.count;
                let price = parseInt(e.contenttext.price.replaceAll(",", ""));
                sum += count * price;
            });
            return sum;
        }
    }

}